'use strict';
/**
 * @api SET_REFRESH() SET_REFRESH
 * @apiGroup APP
 * @apiVersion 1.0.0
 * @apiDescription APP 中 H5 容器添加下拉刷新功能
 * @apiUse APPNpm
 * @apiUse APPJs
 * @apiParamExample {string} 例子
 * // 调用后，可在 h5 容器中可下拉刷新当前页面
 * SET_REFRESH()
 * @apiParamExample {string} 返回值
 * 无
 **/
import {WinJSDec} from './APP/config';

var name = 'SET_REFRESH';
var SET_REFRESH = (enable = true) => {
  WinJSDec(_ => {
    window.WinJSBridge.call('webview', 'pullRefreshEnable', {enable});
  });
};
export default SET_REFRESH;
